import * as Icons from '@mui/icons-material';
import { Box, Button, Container, Grid, Skeleton, Tooltip, Typography } from "@mui/material";
import React, { Suspense } from "react";
import { useTranslation } from "react-i18next";
import { useNavigate } from "react-router-dom";
import styled from 'styled-components';
import { ROUTES } from "../constants";
import { useAppState } from "../state/stateContext";
import { isAdmin } from "../utils/isWho";
import { WhitePaperContainer } from "./documents/styled-components";

const Dashboard = React.lazy(() => import("../components/Dashboard"));

const Homepage: React.FC = () => {
  const { t } = useTranslation();
  const { user } = useAppState();
  const navigate = useNavigate();

  const handleNavigation = (path: string, disabled?: boolean) => {
    if (!disabled) {
      navigate(path);
    }
  };

  const sections = [
    {
      icon: <Icons.Description fontSize="large" />,
      title: t("Documents"),
      items: [
        {
          icon: "Description",
          title: t("HomepageDocumentsCardTitle"),
          description: t("HomepageDocumentsCardDesc"),
          path: ROUTES().documentsList,
          disabled: !Boolean(user),
        },
        {
          icon: "AddCircle",
          title: t("OpenDocumentQuoteTitle"),
          description: t("OpenDocumentQuoteDesc"),
          path: ROUTES().createDocument,
          disabled: !isAdmin(user),
        },
      ]
    },
    {
      icon: <Icons.ShoppingCart fontSize="large" />,
      title: t("Products"),
      items: [
        {
          icon: "ShoppingCart",
          title: t("HomepageProductCardTitle"),
          description: t("HomepageProductCardDesc"),
          path: ROUTES().productList,
          disabled: !Boolean(user),
        },
        {
          icon: "AddCircle",
          title: t("Create Product"),
          description: t("Create a new product"),
          path: ROUTES().createProduct, // Assuming you have a route for creating a product
          disabled: !isAdmin(user),
        },
      ]
    },
    {
      icon: <Icons.Group fontSize="large" />,
      title: t("Users & Clients"),
      items: [
        {
          icon: "Group",
          title: t("HomepageUsersCardTitle"),
          description: t("HomepageUsersCardDesc"),
          path: ROUTES().usersList,
          disabled: !Boolean(user),
        },
        {
          icon: "Person",
          title: t("HomepageClientsCardTitle"),
          description: t("HomepageClientsCardDesc"),
          path: ROUTES().clientsList,
          disabled: !Boolean(user),
        },
      ]
    }
  ];


  return (
    <WhitePaperContainer>
      <Container maxWidth="lg" sx={{ paddingBottom: '4rem' }}>
        <Grid container spacing={4} sx={{ mt: 0 }}>
          {sections.map((section, index) => (
            <Grid item xs={12} sm={6} md={4} key={index}>
              <Box textAlign="center" mb={3}>
                {section.icon}
                <Typography variant="h6" sx={{ mt: 1, fontWeight: 'bold' }}>
                  {section.title}
                </Typography>
              </Box>
              {section.items.map((item, idx) => {
                const IconComponent = Icons[item.icon as keyof typeof Icons];
                return (
                  <Tooltip key={idx} title={item.description} arrow>
                    <StyledButton
                      sx={{ marginBottom: '1rem' }}
                      variant="outlined"
                      startIcon={<IconComponent />}
                      onClick={() => handleNavigation(item.path, item.disabled)}
                      disabled={item.disabled}
                    >
                      <Typography variant="h6" sx={{ fontWeight: 'bold', overflow: 'hidden', textOverflow: 'ellipsis', whiteSpace: 'nowrap' }}>
                        {item.title}
                      </Typography>
                    </StyledButton>
                  </Tooltip>
                );
              })}
            </Grid>
          ))}
        </Grid>
        <Box mt={6}>
          <Suspense fallback={<Skeleton variant="rectangular" height={400} />}>
            <Dashboard />
          </Suspense>
        </Box>
      </Container>
    </WhitePaperContainer>
  );
};

const StyledButton = styled(Button)`
  justify-content: flex-start;
  text-align: left;
  margin-bottom: 16px;
  padding: 16px;
  border-radius: 8px;
  background-color: ${({ disabled }) => (disabled ? '#f5f5f5' : '#fff')};
  color: ${({ disabled }) => (disabled ? '#bdbdbd' : '#000')};
  box-shadow: ${({ disabled }) => (disabled ? 'none' : '0 4px 6px rgba(0, 0, 0, 0.1)')};
  width: 100%;
  height: 100px; /* Standard height */
  &:hover {
    background-color: ${({ disabled }) => (disabled ? '#f5f5f5' : '#f0f0f0')};
  }
`;

export default Homepage;
